import React from 'react';
import useMain from './useMain';
import Animation from './components/Animation';
import Headinfo from './components/Headinfo';
import HourlyForecast from './components/HourlyForecast';
import DailyForecast from './components/DailyForecast';
import CurrentConditions from './components/CurrentConditions';
import WeatherByChooseLocation from './components/WeatherByChooseLocation';
import DataSet from './components/DataSet';

const Main = () => {
  const {
    globalStatus,
    geopositionStatus,
    currentconditions,
    hourlyforecast,
    dailyforecast,
    weatherByUpdate,
    weatherByCityKey
  } = useMain();

  return(
    <React.Fragment>
      <main className='main' role='main'>

        <input type='radio' name='tab' id='tab-nav-1' defaultChecked />
        <label htmlFor="tab-nav-1">Погода</label>
        
        <input type='radio' name='tab' id='tab-nav-2' />
        <label htmlFor='tab-nav-2'>Локації</label>

        <input type='radio' name='tab' id='tab-nav-3' />
        <label htmlFor='tab-nav-3'>Історія</label>

        <div className='container'>

          <div className='tab'>

            { !globalStatus && geopositionStatus !== 'error' &&
              <Animation />
            }
            
            { globalStatus &&
              <React.Fragment>
              
                <section className='head'>
                  <Headinfo {...currentconditions} />
                </section>

                <section className='hourlyforecast'>
                  { hourlyforecast.map((item) => (
                    <HourlyForecast 
                      date={item.Date}
                      key={item.Date} 
                      icon={item.Icon} 
                      alt={item.Alt} 
                      precipitationprobability={item.PrecipitationProbability}
                      temperature={item.Temperature}
                    />
                  ))}
                </section>

                <section className='dailyforecast'>
                  { dailyforecast.map((item) => (
                    <DailyForecast 
                      date={item.Date} 
                      key={item.Date} 
                      icon={item.Icon} 
                      alt={item.IconPhrase} 
                      precipitationprobabilityday={item.PrecipitationProbability} 
                      // precipitationprobabilitynight={item.Night.PrecipitationProbability}
                      sunnyhours={item.HoursOfSun} 
                      temperaturemin={item.Minimum} 
                      temperaturemax={item.Maximum}
                    />
                  ))}
                </section>

                <section className='current-weather'>
                  <CurrentConditions {...currentconditions} />
                </section>

                <button className='refreshbtn' onClick={weatherByUpdate}></button>

              </React.Fragment>
            }

            { geopositionStatus === 'error' && 
              <section className='disablelocation'>
                <h1>Будь ласка, дозвольте доступ до розташування, або ж скористайтесь пошуком, щоб знайти потрібну локацію</h1>
              </section>
            }

          </div>

          <div className='tab location'>
            <WeatherByChooseLocation weatherByCityKey={weatherByCityKey} />
          </div>

          <div className='tab'>
            <DataSet />
          </div>
            
        </div>

      </main>
    </React.Fragment>
  )
}

export default Main;